<template>
	<button
		ref="btnElement"
		:disabled="isExecutingOnClick || (objectInfo.isContents && !objectInfo.editInRow) || isDisableEdit"
		:style="{
			// Background
			backgroundColor: attributes.image ? 'transparent' : attributes.bgColor,
			backgroundImage: attributes.image && `url(${imgPath})`,
			// Border radius
			borderRadius: attributes.borderCornerRadius,
			// Font
			color: isExecutingOnClick ? 'lightgray' : attributes.foreColor,
			fontSize: attributes.fontSize && attributes.fontSize,
			// Align
			justifyContent: (attributes.align.row && attributes.align.row) || 'center',
			alignItems: (attributes.align.column && attributes.align.column) || 'center',
			// Padding
			padding: (!attributes.labelWidth || attributes.labelWidth.toString() === '0' || attributes.title) && !hasPadding && 0,
			// is Executing or disableEdit? -> set opacity
			opacity: isExecutingOnClick || isDisableEdit ? 0.4 : 1,
			cursor: isExecutingOnClick || (objectInfo.isContents && !objectInfo.editInRow) || isDisableEdit ? 'default' : 'pointer',
		}"
		:class="{
			'no-border': attributes.image || !attributes.bgColor || attributes.bgColor === '#00000000' || attributes.bgColor === 'transparent',
			noEditInRow: objectInfo.isContents && !objectInfo.editInRow,
			'keep-aspect-ratio': attributes.keepAspectRatio,
		}"
		@click="!noEditInRow && onClick()"
		@touchstart="onTouchStart"
		@touchend="onTouchEnd"
		@mousedown="onTouchStart"
		@mouseend="onTouchEnd"
		@mouseleave="onTouchEnd"
		@touchleave="onTouchEnd"
		@focus="onFocus"
	>
		{{ attributes.labelWidth !== "0" ? attributes.title : "" }}
	</button>
</template>

<script>
import { inject, ref, Ref, PropType, watch, watchEffect, ComputedRef, onMounted, computed, nextTick } from "vue";

import { xoneAttributesHandler, PropAttributes } from "../../composables/XoneAttributesHandler";
import { XoneDataObject } from "../../composables/appData/core/XoneDataObject";
import { getAppPath, getImagePath } from "../../composables/helperFunctions/ImageHelper";
import dragAndDrop from "../../composables/DragAndDrop";
import { XoneControl } from "../../composables/XoneViewsHandler";

export default {
	props: {
		/**
		 * xoneDataObject
		 * @type {PropType<XoneDataObject>}
		 */
		xoneDataObject: { type: Object, required: true },
		/**
		 * attributes
		 * @type { PropType<PropAttributes>}
		 */
		attributes: { type: Object, default: null, required: true },
		paddings: { type: Object, required: true },
		isDisableEdit: { type: Boolean, required: true },
		containerWidth: { type: Number, default: 0 },
		containerHeight: { type: Number, default: 0 },
	},
	setup(props) {
		// const isDesigner = dragAndDrop.getIsDesigner();
		// on click method is being executed
		const isExecutingOnClick = ref(false);

		/**
		 * on click button
		 */
		const onClick = async () => {
			if (isExecutingOnClick.value) return;
			isExecutingOnClick.value = true;
			await xoneAttributesHandler.onClick(props.attributes, props.xoneDataObject, objectInfo);
			isExecutingOnClick.value = false;
		};

		/**
		 * Contents Info
		 * @type {import('../../composables/AppDataHandler').Objectinfo}
		 */
		const objectInfo = inject("objectInfo");

		//
		// Offset height / width when scaled

		/**
		 * button element
		 * @type {Ref<HTMLElement>}
		 */
		const btnElement = ref();

		const fitSizeToContainer = (isResized = false) => {
			const doFit = () => {
				xoneAttributesHandler.fitHeightToContainer(props.attributes, btnElement, isResized);
				xoneAttributesHandler.fitWidthToContainer(props.attributes, btnElement, isResized);
			};
			setTimeout(() => doFit(), 100);
			doFit();
		};

		nextTick(() => fitSizeToContainer());

		/**
		 * xoneView
		 * @type {XoneView}
		 */
		const xoneView = inject("xoneView");

		const xoneControl = new XoneControl(props.attributes.name, btnElement.value);

		onMounted(() => {
			// Add control to view
			xoneView.addControl(xoneControl);
		});

		/**
		 * Last breadcrumb in stack
		 * @type {ComputedRef<import('../../composables/AppDataHandler').Breadcrumb>}
		 */
		const lastBreadcrumb = inject("lastBreadcrumb");

		// Provide breadcrumbId info to child components
		const breadcrumbId = inject("breadcrumbId");

		watchEffect(() => {
			if (breadcrumbId === lastBreadcrumb?.value?.id) fitSizeToContainer();
		});

		watch(
			() => props.containerWidth,
			() => fitSizeToContainer(true)
		);
		watch(
			() => props.containerHeight,
			() => fitSizeToContainer(true)
		);

		// if selected group is current group fit size to container
		/**
		 * groupId
		 * @type {number}
		 */
		const groupId = inject("groupId");

		/**
		 * activeGroup
		 * @type {Ref<number>}
		 */
		const { activeGroup } = inject("groupHandler");

		watch(
			() => activeGroup.value,
			(newValue) => {
				if (groupId !== newValue) return;
				fitSizeToContainer();
			}
		);

		const imgPath = ref(
			props.attributes.image?.includes("icons") || props.attributes.image?.includes("files")
				? props.attributes.image
				: `${getAppPath()}/icons/${props.attributes.image}`
		);

		onMounted(() => {
			watch(
				() => props.attributes.image,
				async () => await resolveImage()
			);
			resolveImage();
		});

		const resolveImage = async () => (imgPath.value = await getImagePath(props.attributes.image));

		/**
		 * hasPadding
		 * @type {ComputedRef<boolean>}
		 */
		const hasPadding = computed(
			() => props.attributes.paddings.top || props.attributes.paddings.right || props.attributes.paddings.bottom || props.attributes.paddings.left
		);

		// Change opacity on mouse click / touch
		let lastOpacity;
		const onTouchStart = () => {
			lastOpacity = btnElement.value.style.opacity;
			btnElement.value.style.opacity = 0.7;
		};
		const onTouchEnd = () => {
			btnElement.value.style.opacity = lastOpacity;
			lastOpacity = null;
		};
		const onFocus = () => {
			btnElement.value.animate(
				[
					// keyframes
					{ opacity: 0.7 },
					{ opacity: 300 },
				],
				{
					// timing options
					duration: 1000,
				}
			);
		};

		return {
			btnElement,
			onClick,
			objectInfo,
			isExecutingOnClick,
			imgPath,
			hasPadding,
			onTouchStart,
			onTouchEnd,
			onFocus,
		};
	},
};
</script>

<style scoped>
/* button */
button {
	box-sizing: border-box;
	-moz-box-sizing: border-box;
	-webkit-box-sizing: border-box;
	display: flex;
	position: relative;
	width: 100%;
	height: 100%;
	background-repeat: no-repeat;
	background-position: center;
	background-size: contain;
	overflow: hidden;
	cursor: pointer;
	outline: none;
	align-self: center;
	border: var(--border-width) solid var(--border-color);

	padding-top: var(--padding-top);
	padding-right: var(--padding-right);
	padding-bottom: var(--padding-bottom);
	padding-left: var(--padding-left);

	box-shadow: 0px 0px var(--elevation) 0 rgba(0, 0, 0, 0.2);

	animation: fadeIn 0.3s;
}

.keep-aspect-ratio {
	background-size: contain;
}

.no-border {
	border: 1px solid transparent;
}

.noEditInRow {
	cursor: pointer;
	pointer-events: none;
}
</style>
