	/**
	 * pickFile
	 * @param {string|object} PropName
	 * @param {string} Extensions
	 * @returns {Promise<string>}
	 */
	pickFile(PropName, Extensions) {
		return new Promise((resolve) => {
			const inputElement = document.createElement("input");
			inputElement.type = "file";
			inputElement.style.display = "none";

			const fileTypes = typeof PropName === "object" ? PropName.fileTypes : Extensions;
			if (fileTypes)
				inputElement.accept = fileTypes
					.split(",")
					.map((e) => "." + e)
					.join(",");

			inputElement.onchange = (e) =>
				this.uploadFile(e.target.files[0])
					.then((fileName) => {
						if (typeof PropName === "object") PropName.targetProperty = fileName;
						else _xoneDataObject[PropName] = fileName;
					})
					.finally(() => {
						document.body.removeChild(inputElement);
						resolve();
					});

			document.body.appendChild(inputElement);
			inputElement.click();
		});
	}

