	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	border-radius: 5px;
	overflow: hidden;
	background-color: white;
	border: 1px solid black;
	animation: zoomIn 0.3s;
}

.xone-video,
.xone-photo {
	align-items: center;
	margin: 20px;
	background-color: transparent;
	border: none;
	width: 60px;
	height: 60px;
	animation: fadeIn 0.3s;
}

.xone-photo > div,
.xone-video > div {
	display: flex;
	justify-content: center;
	align-items: center;
	border: none;
	overflow: visible;
	border-radius: 100%;
	background-color: transparent;
}

.xone-photo > div > div {
	width: 58px;
	height: 58px;
	border-radius: 100%;
	background: white;
	border: 1px solid gray;
	box-shadow: 0 1px 5px rgba(0, 0, 0, 0.2), 0 2px 2px rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.12);
}

.xone-video > div > div {
	height: 58px;
	width: 58px;
	background: red;
	border: 1px solid gray;
	transition: all 0.3s;
	box-shadow: 0 1px 5px rgba(0, 0, 0, 0.2), 0 2px 2px rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.12);
}

.xone-cancel {
	position: sticky;
	right: 5px;
