	align-self: flex-end;
	margin: 10px 10px 10px 0;
	padding: 5px 10px;
	border: none;
	color: #3273dc;
	border: 1px #3273dc solid;
	border-radius: 5px;
	background-color: transparent;
	font-size: 16px;
}

button {
	outline: none;
}

video {
	animation: fadeIn 0.3s;
}
</style>
