export default class SqlType {
    public static SQLTYPE_ERROR = -1;        /// Error durante el parsing del SQL
    public static SQLTYPE_UNKNOWN = 0;        /// Aún no se ha parseado o se trata de una operación desconocida
    public static SQLTYPE_INSERT = 1;        /// Sentencia INSERT
    public static SQLTYPE_DELETE = 2;        /// Sentencia DELETE
    public static SQLTYPE_UPDATE = 3;        /// Sentencia UPDATE
    public static SQLTYPE_SELECT = 4;        /// Sentencia SELECT
}