  top: 0;
  left: 0;
  position: absolute;
  height: 100vh;
  width: 100vw;
  pointer-events: none;
}

.xone-designer-selected {
  transition: all 0.3s;
  box-sizing: border-box;
  position: absolute;
  width: 100px;
  height: 20px;
  background-color: transparent;
  outline: var(--border);
  max-height: 100vh;
  z-index: 99999;
}

.xone-designer-selected-resizer {
  display: flex;
  position: absolute;
  right: -12.5px;
  bottom: -12.5px;
  justify-content: center;
  align-items: center;
  width: 25px;
  height: 25px;
  pointer-events: all;
  cursor: nw-resize;
}
.xone-designer-selected-resizer > div {
  background: black;
  border-radius: 100%;
  width: 10px;
  height: 10px;
  border: 1px solid white;
}

.xone-designer-selected-options {
  position: absolute;
  display: flex;
  right: 1px;
  pointer-events: all;
}

.xone-designer-selected-options div {
  height: 20px;
  width: 20px;
  border: none;
  color: white;
  margin-left: 2px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: 16px 16px;
  background-color: whitesmoke;
  border-radius: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.xone-designer-selected-options div:hover {
  outline: 1px solid #4cabd5;
}

.xone-designer-selected-options-name {
  width: auto !important;
  color: #1f3c6e !important;
  padding: 0 5px !important;
  background-color: white !important;
}
.xone-designer-selected-options-name:hover {
  outline: none !important;
}

.xone-designer-selected-option-parent {
  background-image: var(--image-parent);
