	PutBitmappedProperty(FieldName: string, Value: any) {
		// Amiyares: 30/06/2021
		const linked = this.getFieldPropertyValue(FieldName, "linkedto");
		if (this[FieldName] === (Value === 1)) return;
		const bit = Number(this.getFieldPropertyValue(FieldName, "bit")) - 1;
		this[linked] &= ~(1 << bit);
		if (Value === 1) this[linked] |= 1 << bit;
		this.setNormalValue(FieldName, Value);
	}

