<template>
	<teleport v-if="state.visible" to="body">
		<div class="xone-msgbox">
			<!-- Standard Msgbox -->
			<div class="xone-msgbox-box" v-if="!state.isCustomMsgbox">
				<div class="xone-msgbox-title">{{ state.title }}</div>
				<div class="xone-msgbox-text">{{ state.msg }}</div>
				<div class="xone-msgbox-options">
					<button ref="btnOkElement" @click="onClick(true)">{{ state.btn1 }}</button>
					<button v-if="state.btn2 !== ''" @click="onClick(false)">
						{{ state.btn2 }}
					</button>
				</div>
			</div>
			<!-- Custom Msgbox -->
			<div class="xone-custom-msgbox" v-if="msgboxBreadcrumb">
				<Coll :breadcrumb="msgboxBreadcrumb" :objectInfo="objectInfo"></Coll>
			</div>
		</div>
	</teleport>
</template>

<script>
import { computed, inject, reactive, Ref, ComputedRef, ref, nextTick } from "vue";

import Coll from "../Coll.vue";
import { setCustomMsgBoxCallback, setMsgBoxCallback } from "../../composables/XoneUI";
import { XoneDataObject } from "../../composables/appData/core/XoneDataObject";
import AppDataHandler, { Breadcrumb } from "../../composables/AppDataHandler";

export default {
	components: {
		Coll,
	},
	setup() {
		const state = reactive({
			isCustomMsgbox: false,
			title: "",
			msg: "",
			flags: 0,
			btn1: "",
			btn2: "",
			response: null,
			visible: false,
		});

		//
		// Standard Msgbox

		setMsgBoxCallback(async (message = "", title = "", flags = 0) => {
			state.isCustomMsgbox = false;
			state.title = title;
			state.msg = message;
			state.flags = flags;
			switch (flags) {
				case 0:
					state.btn1 = "Ok";
					state.btn2 = "";
					break;
				case 1:
					state.btn1 = "Sí";
					state.btn2 = "No";
					break;
				case 4:
					state.btn1 = "Ok";
					state.btn2 = "Cancel";
					break;
			}
			state.response = null;
			state.visible = true;
			await nextTick();
			btnOkElement.value.focus();
			while (!state.response) await new Promise((resolve) => setTimeout(() => resolve(), 100));

			state.visible = false;

			return state.response;
		});

		const onClick = (isAccepted) => {
			switch (state.flags) {
				case 0:
					return (state.response = 10);
				case 1:
					return (state.response = isAccepted ? 1 : 2);
				case 4:
					return (state.response = isAccepted ? 6 : 7);
				default:
					return (state.response = 0);
			}
		};

		//
		// Custom MsgBox

		/**
		 * XOne Web Layout
		 * @type {ComputedRef<Breadcrumb>}
		 */
		const msgboxBreadcrumb = computed(() => {
			const layout = AppDataHandler.getBreadcrumbs().filter((e) => e.isMsgbox);
			return layout.length !== 0 ? layout[0] : null;
		});

		const objectInfo = {
			isContents: false,
			isMsgBox: true,
			onMsgBoxOptionSelected: (res) => {
				AppDataHandler.removeBreadcrumb(msgboxBreadcrumb.value.id);
				return (state.response = !isNaN(Number(res)) ? Number(res) : (state.response = res));
			},
		};

		setCustomMsgBoxCallback(async (dataObject) => {
			state.response = null;

			if (!(dataObject instanceof XoneDataObject)) return;

			AppDataHandler.pushXoneDataObject(dataObject, "Msgbox", false, true);

			state.isCustomMsgbox = true;
			state.visible = true;

			while (!state.response) await new Promise((resolve) => setTimeout(() => resolve(), 100));

			state.visible = false;

			return state.response;
		});

		/**
		 * Window Size
		 * @type {{containerWidth: Ref<number>|ComputedRef<number>, containerHeight: Ref<number>|ComputedRef<number>}}
		 */
		const { containerWidth, containerHeight } = inject("containerSize");

		/** @type {Ref<HTMLButtonElement>} */
		const btnOkElement = ref();

		return {
			state,
			onClick,
			msgboxBreadcrumb,
			containerWidth,
			containerHeight,
			objectInfo,
			btnOkElement,
		};
	},
};
</script>

<style scoped>
