	position: absolute;
	background-color: rgba(0, 0, 0, 0.5);
	top: 0;
	display: flex;
	justify-content: center;
	align-items: center;
	width: 100%;
	height: 100vh;
	pointer-events: all;
	animation: fadeIn 0.3s;
	z-index: 999;
}

.xone-msgbox-box {
	max-width: 95vw;
	max-height: 95vh;
	padding: 20px;
	margin: 10px;
	display: flex;
	flex-direction: column;
	background-color: white;
	border-radius: 5px;
	border: 1px rgba(0, 0, 0, 0.14) solid;
	box-shadow: 0 2px 4px -1px rgba(0, 0, 0, 0.2), 0 4px 5px rgba(0, 0, 0, 0.14), 0 1px 10px rgba(0, 0, 0, 0.12);
	animation: zoomIn 0.3s;
	overflow-x: hidden;
	overflow-y: auto;
}

.xone-msgbox-title {
	margin-bottom: 30px;
	width: 100%;
	font-size: 21px;
	text-align: justify;
}

.xone-msgbox-text {
	width: 100%;
	font-size: 18px;
	text-align: justify;
}

.xone-msgbox-options {
	width: 100%;
	display: flex;
	flex-direction: row;
	justify-content: flex-end;
}

.xone-msgbox-options button {
	margin-top: 30px;
	margin-left: 10px;
	padding: 5px 10px;
	border: none;
	color: #3273dc;
	border: 1px #3273dc solid;
	border-radius: 5px;
	background-color: transparent;
	font-size: 16px;
}

.xone-msgbox-options button:hover {
	cursor: pointer;
}

.xone-custom-msgbox {
	display: flex;
	flex-direction: column;
	animation: fadeIn 0.5s ease, zoomIn 0.3s ease;
}
</style>
