export const replaceLayerName = (layer) => layer.replace("none", "standard").replace("normal", "streets");

export const getDefaultMapLayer = (/** @type {import("../XoneAttributesHandler").PropAttributes} */ attributes) =>
	attributes.viewMode === "mapview" ? "streets" : "standard";

export const loadLeaflet = async () => {
	if (window.L) return;

	// Import leaflet css
	const lCssElement = document.createElement("link");
	lCssElement.setAttribute("rel", "stylesheet");
	lCssElement.setAttribute("href", "modules/leaflet/leaflet.css");
	document.head.append(lCssElement);
	// Import leaflet js
	const lJsElement = document.createElement("script");
	lJsElement.setAttribute("src", "modules/leaflet/leaflet.js");
	document.head.append(lJsElement);
	// Import routing machine css
	const lrmCssElement = document.createElement("link");
	lrmCssElement.setAttribute("rel", "stylesheet");
	lrmCssElement.setAttribute("href", "modules/leaflet/leaflet-routing-machine.css");
	document.head.append(lrmCssElement);
	// Import leaflet routing machine js
	const lrmJsElement = document.createElement("script");
	lrmJsElement.setAttribute("src", "modules/leaflet/leaflet-routing-machine.js");
	document.head.append(lrmJsElement);

	while (!window.L) await new Promise((resolve) => setTimeout(() => resolve(), 1));

	// set imagePath
	L.Icon.Default.imagePath = "./assets/";
};

