    private isEquNode(obj1: XmlNode, obj2: XmlNode, config) {
        if (obj1.nodeName == obj2.nodeName) {
            var max = config.length;
            for (var i = 0; i < max; ++i) {
                if (config[i].nodename == obj1.nodeName) {
                    if (config[i].attrname == '*') {
                        return true;
                    }

                    var attr1 = this.findNodeAttr(obj1, config[i].attrname);
                    if (attr1 == null) {
                        return false;
                    }

                    var attr2 = this.findNodeAttr(obj2, config[i].attrname);
                    if (attr2 == null) {
                        return false;
                    }

                    if (attr1.value == attr2.value) {
                        return true;
                    }

                    return false;
                }
            }
        }

        return false;
    }

    private findSameChild(obj: XmlNode, child: XmlNode, config) {
        var nums = obj.getChildren().length;
        var childs = obj.getChildren();
        for (var i = 0; i < nums; ++i) {
            var curobj = childs[i];
            if (this.isEquNode(curobj, child, config)) {
                return curobj;
            }
        }

        return null;
    }

    private mergeAttrs(obj1: XmlNode, obj2: XmlNode) {
        obj2.attributes.keys().forEach(element => {
            obj1.attributes.setValue(element, obj2.attributes.getValue(element));
        });
    }

    private mergeObj(obj1: XmlNode, obj2: XmlNode, config): XmlNode {
        var isequ = this.isEquNode(obj1, obj2, config);
        if (isequ) {
            this.mergeAttrs(obj1, obj2);
        } else {
            obj1.appendChild(obj2);
            return obj1;
        }

        if (!obj2.hasChildNodes()) {
            return obj1;
        }

        var nums2 = obj2.getChildren().length;
        var childs2 = obj2.getChildren();

        for (var i = 0; i < nums2; ++i) {
            var curobj = childs2[i];
            if (curobj.getName() == '#text') {
                continue;
            }
            //this.mergeObj(obj1child, curobj, config);
            //logger.normal.log('info', 'obj2 ', curobj.nodeName);

            var obj1child = this.findSameChild(obj1, curobj, config);
            if (obj1child != null) {
                //logger.normal.log('info', 'merge ', curobj.nodeName);

                this.mergeObj(obj1child, curobj, config);
            }
            else {
                obj1.appendChild(curobj);
                //this.mergeObj(obj1, curobj, config);
            }
        }
        return obj1;
    }


