  margin: 10px;
  font-weight: bold;
  font-size: 1rem;
} */

table {
	/* flex-shrink: 1; */
	/* align-self: flex-end; */
	table-layout: fixed;
	border-collapse: collapse;
	max-width: var(--linked-search-max-width);
	max-height: var(--linked-search-max-height);
	min-width: var(--linked-search-table-min-width);
	min-height: var(--linked-search-min-height);
	animation: fadeIn 0.3s;
	/* width: fit-content; */
}

td {
	padding: 5px 10px 5px;
	overflow: hidden;
	text-overflow: ellipsis;
	width: fit-content;
	white-space: nowrap;
}

tr {
	white-space: nowrap;
}

tr:hover {
	cursor: pointer;
	background-color: rgba(0, 0, 0, 0.14);
}

th {
	position: -webkit-sticky;
	position: sticky;
	top: 0;
	z-index: 2;
	width: fit-content;
	text-align: left;
}

.xone-search-header:hover {
	background: white;
}

input {
	position: sticky;
	top: 0;
	max-width: var(--linked-search-max-width);
	min-width: var(--linked-search-table-min-width);
	max-height: var(--linked-search-max-height);
	padding: 5px;
	border: none;
	background-color: white;
	background-image: var(--image-search-placeholder);
	background-repeat: no-repeat;
	background-position: left;
	padding-left: 30px;
	background-size: 32px 32px;
}
</style>
